"use client";

import { useEffect, useState } from "react";
import { useRouter } from "next/navigation";
import { Button } from "@/components/ui/button";
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import EmployeeHeader from "@/components/ui/employee/EmployeeHeader";
import BookingsManager from "@/components/ui/employee/bookings";
import { createBrowserClient } from "@supabase/ssr";
import { Building, Calendar, Users, MessageSquare, LogOut, Eye, Mail, Phone, FileText, User } from "lucide-react";
import Link from "next/link";

export default function EmployeeDashboard() {
  const router = useRouter();
  const [employee, setEmployee] = useState<any>(null);
  const [loading, setLoading] = useState(true);
  const [apartments, setApartments] = useState<any[]>([]);
  const [tenants, setTenants] = useState<any[]>([]);
  const [messages, setMessages] = useState<any[]>([]);
  const [searchTerm, setSearchTerm] = useState("");
  const [filterStatus, setFilterStatus] = useState("all");

  const supabase = createBrowserClient(
    process.env.NEXT_PUBLIC_SUPABASE_URL || "",
    process.env.NEXT_PUBLIC_SUPABASE_ANON_KEY || ""
  );

  useEffect(() => {
    const employeeData = localStorage.getItem("employee_session");
    if (!employeeData) {
      router.push("/login?redirect=employee");
      return;
    }
    try {
      setEmployee(JSON.parse(employeeData));
    } catch (err) {
      console.error("Error parsing employee session:", err);
      router.push("/login?redirect=employee");
    }
    setLoading(false);
  }, [router]);

  // Fetch apartments
  useEffect(() => {
    if (!employee) return;

    const fetchApartments = async () => {
      try {
        const { data, error } = await supabase
          .from("apartments")
          .select("*")
          .order("created_at", { ascending: false });

        if (error) throw error;
        setApartments(data || []);
      } catch (err) {
        console.error("Error fetching apartments:", err);
      }
    };

    fetchApartments();
  }, [employee]);

  // Fetch feedback as messages
  useEffect(() => {
    if (!employee) return;

    const fetchMessages = async () => {
      try {
        const { data, error } = await supabase
          .from("client_feedback")
          .select("*")
          .order("created_at", { ascending: false });

        if (error) throw error;
        setMessages(data || []);
      } catch (err) {
        console.error("Error fetching messages:", err);
      }
    };

    fetchMessages();
  }, [employee]);

  // Fetch tenants
  useEffect(() => {
    if (!employee) return;

    const fetchTenants = async () => {
      try {
        const { data, error } = await supabase
          .from("tenants")
          .select("*")
          .order("created_at", { ascending: false });

        if (error) throw error;
        setTenants(data || []);
      } catch (err) {
        console.error("Error fetching tenants:", err);
      }
    };

    fetchTenants();
  }, [employee]);

  const handleLogout = () => {
    localStorage.removeItem("employee_session");
    router.push("/login");
  };

  const handleToggleAvailability = async (id: number, isAvailable: boolean) => {
    try {
      const { error } = await supabase
        .from("apartments")
        .update({ is_available: !isAvailable })
        .eq("id", id);

      if (error) throw error;

      setApartments((prev) =>
        prev.map((apt) =>
          apt.id === id ? { ...apt, is_available: !isAvailable } : apt
        )
      );
    } catch (err) {
      console.error("Error updating apartment availability:", err);
    }
  };

  if (loading) {
    return (
      <div className="flex items-center justify-center min-h-screen">
        <p className="text-gray-600">Loading...</p>
      </div>
    );
  }

  if (!employee) return null;

  return (
    <div className="min-h-screen bg-gray-50">
      <EmployeeHeader employee={employee} onLogout={handleLogout} />

      <main className="container mx-auto px-4 py-8">
        <div className="mb-8 flex justify-between items-center">
          <div>
            <h1 className="text-4xl font-bold mb-2">Employee Dashboard</h1>
            <p className="text-gray-600">
              Welcome, {employee.full_name || employee.username}
            </p>
          </div>
          <Button
            onClick={handleLogout}
            variant="destructive"
            className="flex items-center gap-2"
          >
            <LogOut className="h-4 w-4" />
            Logout
          </Button>
        </div>

        {/* Stats Cards */}
        <div className="grid grid-cols-1 md:grid-cols-4 gap-4 mb-8">
          <Card>
            <CardContent className="pt-6">
              <div className="flex items-center justify-between">
                <div>
                  <p className="text-gray-600 text-sm">Total Apartments</p>
                  <p className="text-3xl font-bold">{apartments.length}</p>
                </div>
                <Building className="h-8 w-8 text-blue-600 opacity-20" />
              </div>
            </CardContent>
          </Card>

          <Card>
            <CardContent className="pt-6">
              <div className="flex items-center justify-between">
                <div>
                  <p className="text-gray-600 text-sm">Available Units</p>
                  <p className="text-3xl font-bold">
                    {apartments.filter((a) => a.is_available).length}
                  </p>
                </div>
                <Calendar className="h-8 w-8 text-green-600 opacity-20" />
              </div>
            </CardContent>
          </Card>

          <Card>
            <CardContent className="pt-6">
              <div className="flex items-center justify-between">
                <div>
                  <p className="text-gray-600 text-sm">New Messages</p>
                  <p className="text-3xl font-bold">
                    {messages.filter((m) => !m.is_read).length}
                  </p>
                </div>
                <MessageSquare className="h-8 w-8 text-purple-600 opacity-20" />
              </div>
            </CardContent>
          </Card>

          <Card>
            <CardContent className="pt-6">
              <div className="flex items-center justify-between">
                <div>
                  <p className="text-gray-600 text-sm">Total Messages</p>
                  <p className="text-3xl font-bold">{messages.length}</p>
                </div>
                <Users className="h-8 w-8 text-orange-600 opacity-20" />
              </div>
            </CardContent>
          </Card>
        </div>

        {/* Tabs */}
        <Tabs defaultValue="apartments" className="space-y-8">
          <TabsList className="grid w-full grid-cols-4">
            <TabsTrigger value="apartments" className="flex items-center gap-2">
              <Building className="h-4 w-4" />
              <span className="hidden sm:inline">Apartments</span>
            </TabsTrigger>
            <TabsTrigger value="bookings" className="flex items-center gap-2">
              <Calendar className="h-4 w-4" />
              <span className="hidden sm:inline">Bookings</span>
            </TabsTrigger>
            <TabsTrigger value="tenants" className="flex items-center gap-2">
              <Users className="h-4 w-4" />
              <span className="hidden sm:inline">Tenants</span>
            </TabsTrigger>
            <TabsTrigger value="messages" className="flex items-center gap-2">
              <MessageSquare className="h-4 w-4" />
              <span className="hidden sm:inline">Messages</span>
            </TabsTrigger>
          </TabsList>

          {/* Apartments Tab */}
          <TabsContent value="apartments">
            <Card>
              <CardHeader>
                <CardTitle>Available Apartments</CardTitle>
              </CardHeader>
              <CardContent>
                {apartments.length === 0 ? (
                  <p className="text-gray-500 text-center py-8">
                    No apartments found
                  </p>
                ) : (
                  <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
                    {apartments.map((apt) => (
                      <div
                        key={apt.id}
                        className="border rounded-lg p-4 hover:shadow-md transition-shadow"
                      >
                        {apt.image_url && (
                          <img
                            src={apt.image_url}
                            alt={apt.name}
                            className="w-full h-40 object-cover rounded-lg mb-3"
                          />
                        )}
                        <h3 className="font-semibold text-lg mb-2">{apt.name}</h3>
                        <p className="text-sm text-gray-600 mb-2">
                          {apt.description}
                        </p>
                        <div className="grid grid-cols-2 gap-2 text-sm mb-3">
                          <div>
                            <p className="text-gray-600">Bedrooms</p>
                            <p className="font-semibold">{apt.bedrooms}</p>
                          </div>
                          <div>
                            <p className="text-gray-600">Bathrooms</p>
                            <p className="font-semibold">{apt.bathrooms}</p>
                          </div>
                          <div>
                            <p className="text-gray-600">Size</p>
                            <p className="font-semibold">{apt.size_sqm} mÂ²</p>
                          </div>
                          <div>
                            <p className="text-gray-600">Price</p>
                            <p className="font-semibold">
                              RWF {apt.price_per_month}
                            </p>
                          </div>
                        </div>
                        <div className="flex justify-between items-center mb-3">
                          <span
                            className={`px-2 py-1 rounded text-sm font-medium ${
                              apt.is_available
                                ? "bg-green-100 text-green-800"
                                : "bg-red-100 text-red-800"
                            }`}
                          >
                            {apt.is_available ? "Available" : "Occupied"}
                          </span>
                        </div>
                        <Button
                          onClick={() => handleToggleAvailability(apt.id, apt.is_available)}
                          variant={apt.is_available ? "destructive" : "default"}
                          className="w-full"
                        >
                          {apt.is_available ? "Mark as Occupied" : "Mark as Available"}
                        </Button>
                      </div>
                    ))}
                  </div>
                )}
              </CardContent>
            </Card>
          </TabsContent>

          {/* Bookings Tab */}
          <TabsContent value="bookings">
            <div className="space-y-4">
              <div className="flex justify-end mb-4">
                <Button asChild className="gap-2 bg-blue-600 hover:bg-blue-700">
                  <Link href="/employee/bookings">
                    <Eye className="h-4 w-4" />
                    View All Bookings
                  </Link>
                </Button>
              </div>
              <BookingsManager />
            </div>
          </TabsContent>

          {/* Tenants Tab */}
          <TabsContent value="tenants">
            <Card>
              <CardHeader>
                <CardTitle>Tenants</CardTitle>
              </CardHeader>
              <CardContent>
                <p className="text-gray-500 text-center py-8">
                  Tenant management coming soon
                </p>
              </CardContent>
            </Card>
          </TabsContent>

          {/* Messages Tab */}
          <TabsContent value="messages">
            <Card>
              <CardHeader>
                <CardTitle>Client Feedback</CardTitle>
              </CardHeader>
              <CardContent>
                {messages.length === 0 ? (
                  <p className="text-gray-500 text-center py-8">
                    No messages yet
                  </p>
                ) : (
                  <div className="space-y-4">
                    {messages.map((msg) => (
                      <div
                        key={msg.id}
                        className={`border rounded-lg p-4 ${
                          msg.is_read ? "bg-white" : "bg-blue-50"
                        }`}
                      >
                        <div className="flex justify-between items-start mb-2">
                          <div>
                            <h3 className="font-semibold">{msg.name}</h3>
                            <p className="text-sm text-gray-600">{msg.email}</p>
                          </div>
                          <span
                            className={`px-2 py-1 rounded text-sm font-medium ${
                              msg.is_read
                                ? "bg-gray-100 text-gray-800"
                                : "bg-blue-100 text-blue-800"
                            }`}
                          >
                            {msg.is_read ? "Read" : "New"}
                          </span>
                        </div>
                        <p className="text-gray-700">{msg.message}</p>
                        <p className="text-xs text-gray-500 mt-2">
                          {new Date(msg.created_at).toLocaleDateString()}
                        </p>
                      </div>
                    ))}
                  </div>
                )}
              </CardContent>
            </Card>
          </TabsContent>
        </Tabs>
      </main>
    </div>
  );
}